      height: 0.8rem;
      border-bottom: 1px solid #eaeaea;
      width: 8.1rem;
      .server_model {
        width: 0.7rem;
        text-align: center;
        line-height: 0.8rem;
        float: left;
      }
      .choice {
        float: left;
        padding-left: 0.4rem;
        line-height: 0.8rem;
        span {
          padding-left: 0.1rem;
        }
      }
      .tol {
        float: right;
        line-height: 0.8rem;
      }
    }
  }
  // .classify-box:hover ~ .mouse-enter {
  //   display: block;
  // }
  .btn {
    width: 100%;
    display: block;
    height: 0.7rem;
    .back {
      font-size: 0.14rem;
      padding: 0.4rem 0 0 0.2rem;
      height: 0.33rem;
      float: left;
      display: block;
      width: 0.8rem;
      cursor: pointer;
      color: #000;
    }
    .link {
      width: 1.5rem;
      display: block;
      height: 0.7rem;
      float: right;
      button {
        display: block;
        margin-top: 0.37rem;
        width: 1.5rem;
        height: 0.4rem;
        border: 0;
        line-height: 0.4rem;
        background-color: #3cc8b4;
        color: rgba(255, 255, 255, 1);
        font-size: 0.16rem;
        text-align: center;
        font-family: Roboto;
        border-radius: 0.03rem;
        cursor: pointer;
        outline: none;
        transition: 0.3s;
        &:hover {
          background: #39beab;
        }
      }
    }
  }
}
</style>



