<template>
  <div class="attachment">
    <div class="file" v-if="!isBtn">
      <p>请添加项目附件和补充信息</p>
      <div class="progress">
        <span class="circle"></span>
        <span class="circle"></span>
        <span class="circle"></span>
        <span class="big-circle"></span>
        <span class="circle c"></span>
        <span class="circle c"></span>
      </div>
      <div class="content">
        <div class="pro-prog">
          <div class="title">项目开始时间</div>
          <div class="con">
            <div class="choose">
              <img src="../assets/img/upload.png" alt="">
              <input type="file">
            </div>
          </div>
        </div>
        <div class="pro-progress target">
          <div class="title">补充信息</div>
          <div class="target-con">
            <textarea placeholder="字数请控制在500以内"
            maxlength="500"
            @input="descInput"
            v-model="desc"
            ></textarea>
            <div class="limit">{{remnant}}/500</div>
          </div>
        </div>
        <div class="btn">
          <router-link class="back" to="SpecificDemand">返回</router-link>
          <router-link class="link" :to="{path: '/ChoosePage/introcompany'}">
            <button>继 续</button>
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'attachment',
  data () {
    return {
      isBtn: false,
      desc: '',
      remnant: 500
    }
  },
  mounted () {
  },
  methods: {
    next () {
      this.isBtn = true
    },
    descInput () {
      let val = this.desc.length
      this.remnant = 500 - val
    }
  }
}
</script>

<style lang="less" escoped>
* {
  outline: none;
}
.attachment {
  width: 8.1rem;
  font-size: 0.14rem;
  margin: 0.75rem auto;
  p {
    font-size: 0.2rem;
    padding-bottom: 0.3rem;
  }
  .progress {
    border-bottom: 1px solid #eaeaea;
    margin-bottom: 0.3rem;
    position: relative;
    height: 0;
    font-size: 0;
    .circle {
      width: 0.11rem;
      height: 0.11rem;
      display: block;
      background: #3cc8b4;
      left: 0;
      top: -0.05rem;
      position: absolute;
      border-radius: 50%;
      &:nth-of-type(2) {
        left: 162px;
      }
      &:nth-of-type(3) {
        left: 324px;
      }
    }
    .c {
      width: 12px;
      height: 12px;
      top: -5px;
      display: block;
      background: #eaeaea;
      &:nth-of-type(5) {
        left: 648px;
      }
      &:nth-of-type(6) {
        left: 810px;
      }
    }
    .big-circle {
      width: 0.18rem;
      height: 0.18rem;
      display: block;
      background: #3cc8b4;
      left: 486px;
      top: -0.08rem;
      position: absolute;
      border-radius: 50%;
    }
  }
  .content {
    width: 8.1rem;
    min-height: 500px;
    // border: 1px solid #eaeaea;
    position: relative;
  }
  .btn {
    width: 100%;
    display: block;
    height: 0.8rem;
    .back {
      font-size: 0.14rem;
      padding: 0.4rem 0 0 0.2rem;
      height: 0.33rem;
      float: left;
      display: block;
      width: 0.8rem;
      cursor: pointer;
      color: #000;
    }
    .link {
      width: 1.5rem;
      display: block;
      height: 0.8rem;
      float: right;
      button {
        display: block;
        margin-top: 0.37rem;
        width: 1.5rem;
        height: 0.4rem;
        border: 0;
        line-height: 0.4rem;
        background-color: #3cc8b4;
        color: rgba(255, 255, 255, 1);
        font-size: 0.16rem;
        text-align: center;
        font-family: Roboto;
        border-radius: 0.03rem;
        cursor: pointer;
        outline: none;
        transition: 0.3s;
        &:hover {
          background: #39beab;
        }
      }
    }
  }

  .file {
    .title {
      height: 100%;
      font-size: 0.14rem;
      width: 2.3rem;
      line-height: 0.26rem;
      padding-top: 0.4rem;
      text-align: center;
      float: left;
    }
    .pro-prog {
      overflow: hidden;
      height: 3.7rem;
      border-bottom: 1px solid #eaeaea;
      width: 100%;
      .con {
        font-size: 0.14rem;
        padding-top: 0.4rem;
        .choose {
          width: 5.7rem;
          height: 2.6rem;
          border: 1px dotted #eaeaea;
          background: #F2F2F2;
          float: left;
          img {
            width: 200px;
            display: block;
            margin: 0 auto;
          }
          input {
            margin-left: 200px;
          }
        }
      }
    }
    .target {
      height: 2rem;
      border-bottom: 1px solid #eaeaea;
      overflow: hidden;
      .target-con {
        height: 1rem;
        width: 4.4rem;
        border: 1px solid #eaeaea;
        float: left;
        margin-top: 0.4rem;
        position: relative;
        .limit {
          color: #666;
          position: relative;
          bottom: -4px;
          left: 3.9rem;
          width: 80px;
          font-size: 12px;
        }
        textarea {
          display: block;
          width: 4.3rem;
          height: 100%;
          border: 0;
          line-height: 0.3rem;
          outline: none;
          padding-left: 0.1rem;
        }
      }
    }
  }
}
</style>



